.base {
  transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -webkit-transition: all 0.25s ease-in-out;
  width: 100%;
  height: 100%;
  position: relative;
}
.base video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.base .a {
  width: 100%;
  height: 100%;
  border-radius: 3px;
  cursor: pointer;
  background-color: black;
}
.base .a video {
  border-radius: inherit;
  opacity: 0.25;
  transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -webkit-transition: all 0.25s ease-in-out;
}
.base .a video:hover {
  opacity: 1;
}
.base .b {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  cursor: pointer;
  background-color: black;
}
.base .b video {
  border-radius: inherit;
  opacity: 0.25;
  transition: all 0.25s ease-in-out;
  -moz-transition: all 0.25s ease-in-out;
  -webkit-transition: all 0.25s ease-in-out;
}
.base .b video:hover {
  opacity: 1;
}
.base .cnt {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  pointer-events: none;
}

.react-calendar {
  width: 350px;
  max-width: 100%;
  background: white;
  border: 1px solid #a0a096;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
}
.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  box-sizing: border-box;
}
.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
}
.react-calendar button:enabled:hover {
  cursor: pointer;
}
.react-calendar__navigation {
  height: 44px;
  margin-bottom: 1em;
}
.react-calendar__navigation button {
  min-width: 44px;
  background: none;
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #e6e6e6;
}
.react-calendar__navigation button[disabled] {
  background-color: #f0f0f0;
}
.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75em;
}
.react-calendar__month-view__weekdays__weekday {
  padding: 0.5em;
}
.react-calendar__month-view__weekNumbers {
  font-weight: bold;
}
.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75em;
  padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
}
.react-calendar__month-view__days__day--weekend {
  color: #d10000;
}
.react-calendar__month-view__days__day--neighboringMonth {
  color: #757575;
}
.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
}
.react-calendar__tile {
  max-width: 100%;
  text-align: center;
  padding: 0.75em 0.5em;
  background: none;
}
.react-calendar__tile:disabled {
  background-color: #f0f0f0;
}
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #e6e6e6;
}
.react-calendar__tile--hasActive {
  background: #76baff;
}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
  background: #a9d4ff;
}
.react-calendar__tile--active {
  background: #006edc;
  color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #1087ff;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #e6e6e6;
}

.calendar {
  padding: 5px;
  font-family: var(--font-family);
}
.calendar h1 {
  font-size: var(--font-size-h1-xxl);
}
.calendar .body {
  width: 100%;
  height: 100%;
  padding: 25px;
  border-radius: 4px;
  font-size: var(--font-size-xxl);
  background-color: var(--color-secondary);
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: var(--color-highlight);
  border-radius: 4px;
}
.calendar .body .react-calendar__month-view__weekdays__weekday {
}
.calendar .body .react-calendar__navigation {
  padding-left: 15%;
  padding-right: 15%;
}
.calendar .body .react-calendar__navigation button {
  font-size: var(--font-size-h1-xxl);
  color: var(--color-primary);
}
.react-calendar__navigation .react-calendar__navigation__label {
}
.calendar .body .tile {
}
.calendar .body .react-calendar__tile {
  font-size: var(--font-size-xxl);
  color: var(--color-primary);
}
.calendar .body .react-calendar__tile:enabled:hover,
.calendar .body .react-calendar__tile:enabled:focus {
  background-color: var(--color-highlight);
}
.calendar .body .react-calendar__tile--active {
  background: transparent;
  color: var(--color-primary);
}
.calendar .body .content {
  height: 10vw;
}
@media only screen and (max-width: 1800px) {
  .calendar h1 {
    font-size: var(--font-size-h1-xl);
  }
  .calendar .body .react-calendar__navigation button {
    font-size: var(--font-size-h1-xl);
  }
  .calendar .body .react-calendar__tile {
    font-size: var(--font-size-xl);
  }
}
@media only screen and (max-width: 1200px) {
  .calendar h1 {
    font-size: var(--font-size-h1-lg);
  }
  .calendar .body .react-calendar__navigation button {
    font-size: var(--font-size-h1-lg);
  }
  .calendar .body .react-calendar__tile {
    font-size: var(--font-size-lg);
  }
}
@media only screen and (max-width: 900px) {
  .calendar h1 {
    font-size: var(--font-size-h1-md);
  }
  .calendar .body .react-calendar__navigation button {
    font-size: var(--font-size-h1-md);
  }
  .calendar .body .react-calendar__tile {
    font-size: var(--font-size-md);
  }
}
@media only screen and (max-width: 600px) {
  .calendar h1 {
    font-size: var(--font-size-h1-sm);
  }
  .calendar .body {
    font-size: var(--font-size-sm);
  }
  .calendar .body .react-calendar__navigation button {
    font-size: var(--font-size-h1-sm);
  }
  .calendar .body .react-calendar__tile {
    font-size: var(--font-size-sm);
  }
}

