html {
  /* --- Font Sizes --- */
  --font-size-h1-xxl: 30px;
  --font-size-h1-xl: 26px;
  --font-size-h1-lg: 22px;
  --font-size-h1-md: 18px;
  --font-size-h1-sm: 14px;
  --font-size-xxl: 24px;
  --font-size-xl: 20px;
  --font-size-lg: 16px;
  --font-size-md: 12px;
  --font-size-sm: 8px;
}

/* --- Palettes --- */
.a {
  /* Primary Text */
  --color-primary: rgb(255, 255, 255);
  /* Primary Background */
  --color-secondary: rgb(42, 42, 42);
  /* Primary Text Alt */
  --color-tertiary: rgba(255, 255, 255, 0.5);
  /* Primary Background Alt */
  --color-quaternary: rgb(32, 32, 32);
  /* Primary Highlight*/
  --color-highlight: rgba(0, 224, 255, 0.5);
  --color-quinary: blue;
  --color-senary: blue;
  --font-family: "Roboto";
}

